import { Component, OnInit, ViewChild, ElementRef, Input, Output, EventEmitter, Optional, Inject } from '@angular/core';
import { DashboardService } from '../../service/dashboard-service.service';
import { DashboardMessage } from '../../service/model/DashboardMessage';
import { LoginInfo, Language } from '@allianzSND/core';
import { showDashboardRuleToken } from '../../injectionToken/injection-token';
import { showDashboardRule } from '../../interface/showDashboardRule.interface';



@Component({
  selector: 'app-dashboard-detail',
  templateUrl: './dashboard-detail.component.html',
  styleUrls: ['./dashboard-detail.component.scss']
})
export class DashboardDetailComponent implements OnInit {
  public _messageList : Array<DashboardMessage> = [];
  public windowWidth;
  public _loginInfo: LoginInfo;
  public language = new Language();
  public userImgUrl: string = '';
  public userAgentName: string = '';
  public tabIndex = 0;
  public userLevel: any = null;

  @Input()
  set messageList(messageList) {
    
    this._messageList = messageList;

    console.log("detail message: ", this._messageList);
  }
  get messageList() {
    return this._messageList;
  }

  @Input()
  get loginInfo() {
    return this._loginInfo;
  }
  set loginInfo(loginInfo) {
    this._loginInfo = loginInfo;

    if(this.showDashboardRule) {
      this.showDashboardRule.dashboardShowLoginInfo(loginInfo).subscribe(data => {
        this.userImgUrl = data.imgUrl;
        this.userAgentName = data.showName;
      });
     }

  }

  @Output() onClickLink = new EventEmitter();
  @Output() filterByKeyword = new EventEmitter();
  @Output() clickMessage = new EventEmitter();
  @Output() changeCommitmentStatus = new EventEmitter();
  
  
  tabChangeEvent(event) {
    this.tabIndex = event;
  }

  constructor(
    @Optional() @Inject(showDashboardRuleToken) private showDashboardRule: showDashboardRule,
    private dashboardService: DashboardService,
    
    ) { 
      this.init();
    }

  ngOnInit() {
    this.windowWidth = window.innerWidth;
  }

  
  private init() {
    this.userLevel = this.dashboardService.getUserLevel();
  }

  ClickListShow(){
    this.onClickLink.emit();
  }
  doFilterByKeyword(keyword){
    this.filterByKeyword.emit(keyword);
  }
  
  onChangeCommitmentStatus(event){
    this.changeCommitmentStatus.emit(event);
  }


  doMessageClick(message:DashboardMessage){
    this.clickMessage.emit(message);
  }
    
  


}
