
export class DashboardMessage {

    private _clientID: string;
    private _messageID: number;
    private _messageCategory: string;
    private _messageType: string;
    private _title: string;
    private _description: string;
    private _argument: string;
    private _status: string;
    private _messageDate: string;
    private _messageTime: string;
    private _isPopup: string;
    private _linkStatus: string;
    private _isClick: string;

    constructor(clientID, messageID, messageCatogory, messageType, title, description, argument, status, messageDate, messageTime, isPopup , linkStatus , isClick) {
        
        this._clientID = clientID;
        this._messageID = messageID;
        this._messageCategory = messageCatogory;
        this._messageType = messageType;
        this._title = title;
        this._description = description;
        this._argument = argument;
        this._status = status;
        this._messageDate = messageDate;
        this._messageTime = messageTime;
        this._isPopup = isPopup;
        this._linkStatus = linkStatus;
        this._isClick = isClick;
    }

    get clientID() {
        return this._clientID;
    }
    
    get messageID() { 
        return this._messageID; 
    }

    get messageCategory() {
        return this._messageCategory;
    }

    get messageType() {
        return this._messageType;
    }

    get title() {
        return this._title;
    }

    get description() {
        return this._description;
    }

    get argument() {
        return this._argument;
    }

    get status() {
        return this._status;
    }

    get messageDate() {
        return this._messageDate;
    }

    get messageTime() {
        return this._messageTime;
    }

    get isPopup() {
        return this._isPopup;
    }

    get linkStatus() {
        return this._linkStatus;
    }

    get isClick() {
        return this._isClick;
    }

    set messageID ( messageID ) { 
        this._messageID = messageID; 
    }

    set messageCategory( messageCatogory ) {
        this._messageCategory = messageCatogory;
    }

    set messageType( messageType ) {
        this._messageType = messageType;
    }

    set title( title ) {
        this._title = title;
    }

    set description( description ) {
        this._description = description;
    }

    set argument( argument ) {
        this._argument = argument;
    }

    set status( status ) {
        this._status = status;
    }

    set messageDate( messageDate ) {
        this._messageDate = messageDate;
    }

    set messageTime( messageTime ) {
        this._messageTime = messageTime;
    }

    set isPopup( isPopup ) {
        this._isPopup = isPopup;
    }

    set linkStatus( linkStatus ) {
        this._linkStatus = linkStatus;
    }

    set isClick( isClick ) {
        this._isClick = isClick;
    }





    

}