import { Component, OnInit, Inject, Optional } from '@angular/core';
import { LoginMgrToken, ILoginMgr, Language, DefaultLoginMgr } from '@allianzSND/core';
import { take } from 'rxjs/operators';
import { CONTENTGAP } from '@allianzSND/ui';
import { ROLE } from '@allianzSND/goal';
// import { CONTENTGAP } from 'projects/ui/src/lib/components/ui-content-b-gap/ui-content-b-gap-enum';
// import { CONTENTGAP } from 'projects/ui/src/public_api';

@Component({
  selector: 'snd-dashboard-performance',
  templateUrl: './dashboard-performance.component.html',
  styleUrls: ['./dashboard-performance.component.scss']
})
export class DashboardPerformanceComponent implements OnInit {

  public GAP_SIZE_20 = CONTENTGAP.GAP20;

  public role: string = null;
  public language = new Language();
  public tabIndex: number = 0;


  constructor(
    private loginMgr: DefaultLoginMgr,
  ) {
    this.init();
    console.debug("check start ......");
  }

  ngOnInit() {}

  //LEADER  //AG   <--- page
  private async init() {
    let loginInfo = await this.loginMgr.getLoginInfo().pipe(take(1)).toPromise();

    console.debug("dashboard login info: ", loginInfo);

    let identity = loginInfo.AppUseMode[loginInfo.AppUseMode.length - 1];

    console.debug("dashboard identity: ", identity);

    let agentRole = this._ConvertIdentityToRolesType(identity);//AG

    console.debug("dashboard agent rolepage: ", agentRole);

    this._ShowRolePage(agentRole);
  }

  tabChangeEvent(index) {
    this.tabIndex = index;
  }

  private _ShowRolePage(role: ROLE){
    switch(role) {
      case ROLE.AGENT:
        this.role = "AG";
        break;
      case ROLE.AGENTLEADER:
        this.role = "LEADER";
        break;
      case ROLE.ZONEHEAD:
      case ROLE.CAO:
        this.role = "CAO";
        break;
    }
    //this.role = (role == ROLE.AGENT) ? "AG": "LEADER";
  }

  private _ConvertIdentityToRolesType(identity: string): ROLE {
    //AG AL Manager Supervisor
    switch (identity) {
      case "AG":
        return ROLE.AGENT;
      case "AL":
        return ROLE.AGENTLEADER;
      case "Manager":
        return ROLE.ZONEHEAD;
      case "Supervisor":
        return ROLE.CAO;
    }
  }

  

}
