import { Component, OnInit, Inject, HostListener, ChangeDetectionStrategy, Optional, ChangeDetectorRef } from '@angular/core';
import { DashboardService } from '../../service/dashboard-service.service';

import { addDays, subDays, startOfDay, endOfDay, format } from 'date-fns';
import { LoginMgrToken, ConfigToken, ILoginMgr, LoginInfo, DeviceService, ProfileCode, TranslateService, ProfileCodeService, Language, PageInfo, SelectOption, DefaultLoginMgr, APIFactory, APIDispatch, DataSyncService, NotificationMgr } from '@allianzSND/core';
import { DashboardMessage } from '../../service/model/DashboardMessage';
import { CustomerService, CustomerBirthday, CustomerAlertItem, CalendarService, CalendarEventDetail, CustomerFilterCriteria, CustomerUtils } from '@allianzSND/integration-calendar-customer';
import { Observable } from 'rxjs';

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class DashboardComponent implements OnInit {
  public hello_str = '';
  public isLoadingFinish = true;
  public isRefreshFinish = true;
  public isLazyLoading = true;
  public isMobilePopupOpen = false;

  public dashboardMessageList: Array<DashboardMessage> = [];
  public calendarEventList: Array<CalendarEventDetail> = [];
  public calendarEventMonthList: Array<CalendarEventDetail> = [];
  public birthdayList: Array<CustomerBirthday> = [];
  public searchRangeDate;
  public windowWidth;
  public language: Language = new Language();
  public todate: Date = new Date();
  public dashboardLoginInfo: LoginInfo;
  public alertAutoDeleteCustomer: Array<CustomerAlertItem> = [];
  public alertOverTimeList: Array<CustomerAlertItem> = [];
  public isDisplayDeleteRemind: boolean = false;
  public isDisplayUpdateRemind: boolean = false;
  public searchBirthdaySubN: number;
  public searchBirthdayAddN: number;
  public searchCalendarSubN: number;
  public searchCalendarAddN: number;
  public showCalendarList = [];
  public currentMessageKeyword: string;
  public messagePageInfo = new PageInfo();

  // calendar
  public isExpandDetail = false;
  public isCalendarDelete = false;
  public calendarEventDetail: CalendarEventDetail;
  public calendarItemClientID: string;
  public isPopupDeleteSuccess: boolean = false;
  public isExpandEdit: boolean = false;
  public isDisplaySavePopup: boolean = false;
  public translateMap: Map<string, string> = new Map<string, string>();
  public optionMap: Map<string, Array<ProfileCode>> = new Map<string, Array<ProfileCode>>();
  public viewDate: Date;
  public isSaveClick: boolean = false;
  // translate
  public activityTypeList: Array<ProfileCode> = []; // DBactivityType
  public alertTypeList: Array<ProfileCode> = [];
  private dayTypesList: Array<string> = ['Cross_Day', 'All_Day'];
  private weekdaysList: Array<string> = ['Sun', 'Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat'];
  private monthsList: Array<string> = ['January', 'February', 'March', 'April', 'May', 'June', 'July', 'August', 'September', 'October', 'November', 'December'];
  private noSchedule: string = 'No_Schedule';
  //calendar end

  public customerClientMap: Map<string, string> = new Map<string, string>();
  public customerItemList: Array<SelectOption> = [];
  public customerClientName: string = '';

  constructor(
    private customerUtils: CustomerUtils,
    private dashboardService: DashboardService,
    private calendarService: CalendarService,
    private customerService: CustomerService,
    private changeDetector: ChangeDetectorRef,
    private translateService: TranslateService,
    private profileCodeService: ProfileCodeService,
    private loginMgr: DefaultLoginMgr,
    private dataSyncService: DataSyncService,
    private notificationMgr: NotificationMgr,
    @Inject(ConfigToken) private APP_CONFIG: any,
  ) {


    this.searchBirthdaySubN = this.APP_CONFIG[this.APP_CONFIG.ENV].DASHBOARD.SearchBirthdayRange.subN;
    this.searchBirthdayAddN = this.APP_CONFIG[this.APP_CONFIG.ENV].DASHBOARD.SearchBirthdayRange.addN;
    this.searchCalendarSubN = this.APP_CONFIG[this.APP_CONFIG.ENV].DASHBOARD.SearchCalendarRange.subN;
    this.searchCalendarAddN = this.APP_CONFIG[this.APP_CONFIG.ENV].DASHBOARD.SearchCalendarRange.addN;
  }

  ngOnInit() {
    //init calendar type

    this.messagePageInfo.pageSize = 5;
    this.activityTypeList = this.profileCodeService.getCodeArray('Calendar_Type');
    this.alertTypeList = this.profileCodeService.getCodeArray('Calendar_RemindTime');
    this.optionMap.set('Calendar_Type', this.activityTypeList);
    this.optionMap.set('Calendar_RemindTime', this.alertTypeList);
    this.dayTypesList.forEach((dayType) => {
      this.translateMap.set(dayType, this.translateService.translate(dayType))
    });
    this.weekdaysList.forEach((weekday) => {
      this.translateMap.set(weekday, this.translateService.translate(weekday))
    });
    this.monthsList.forEach((month) => {
      this.translateMap.set(month, this.translateService.translate(month))
    });
    this.translateMap.set(this.noSchedule, this.translateService.translate(this.noSchedule));


    this.windowWidth = window.innerWidth;
    this.isMobilePopupOpen = (this.windowWidth < 1024 ? true : false) && (this.dashboardService.isFirstTime());


    this.viewDate = this.todate;

    this.getLoginInfo();

    this.getCalendarList();

    this.getCustomerBirthdayList(this.todate, this.searchBirthdaySubN, this.searchBirthdayAddN);

    //get UnRead message
    this.doFilterByKeyword("UnRead", false);

    //5/24
    //this.changeDetector.detectChanges();

    this.createCustomerList();

  }
  @HostListener('window:resize', ['$event'])
  onResize(event) {

    this.windowWidth = event.target.innerWidth;
    this.changeDetector.markForCheck();
    if (this.windowWidth < 1024) {
      this.isMobilePopupOpen = true;
    }
    // console.warn('Width: ', this.windowWidth);
  }


  private async getCalendarList() {

    let searchCalendarStart = subDays(this.todate, this.searchCalendarSubN);

    let calendarList = [];

    let PromiseList: Array<Promise<any>> = [];

    for (var i = 0; i < this.searchCalendarSubN + this.searchCalendarAddN + 1; i++) {
      let targetDate = addDays(searchCalendarStart, i);

      PromiseList.push(new Promise((res, rej) => {
        this.getCalendarBetweenDate(startOfDay(targetDate), endOfDay(targetDate)).then((data) => {
          let item = {
            showDate: targetDate,
            eventList: data,
            isBeforeToday: startOfDay(this.todate).getTime() > startOfDay(targetDate).getTime(),
            isToday: startOfDay(this.todate).getTime() == startOfDay(targetDate).getTime()
          }

          res(item);
        });
      }));
    }

    Promise.all(PromiseList).then((list) => {
      this.showCalendarList = list;
      console.log("this.showCalendarList: ", this.showCalendarList);
      //5/24
      //this.changeDetector.detectChanges();
    });






  }

  // scroll load content
  public async loadContent(event) {

    this.messagePageInfo.nextPage();
    await this.doFilterByKeyword(this.currentMessageKeyword, true);
    setTimeout(() => {
      this.isLoadingFinish = true;
      this.changeDetector.detectChanges();
    });
  }
  //refreshContent
  public async refreshContent(event) {

    this.messagePageInfo.resetPage();
    // this.getCalendarList();

    // this.getCustomerBirthdayList(this.viewDate, this.searchBirthdaySubN, this.searchBirthdayAddN);

    // await this.dataSyncService.syncFunc(["DASHBOARD"], true);
    await this.notificationMgr.fetchMessageData();

    //get message
    // await this.getDashboardMessageList();
    await Promise.all([this.doFilterByKeyword(this.currentMessageKeyword, false), this.getCalendarList(), this.getCustomerBirthdayList(this.viewDate, this.searchBirthdaySubN, this.searchBirthdayAddN)]);
    console.log("PromiseAll finish");
    setTimeout(() => {
      this.isRefreshFinish = true;
      //5/24
      //this.changeDetector.detectChanges();
    }, 500);



  }

  private async getLoginInfo() {
    return new Promise((res, rej) => {
      this.loginMgr.getLoginInfo().subscribe((info) => {
        this.dashboardLoginInfo = info;
        console.warn('this.dashboardLoginInfo: ', this.dashboardLoginInfo);
        res();
      })
    });
  }


  public doFilterByKeyword(keyword, isAppend): Promise<any> {
    // console.log("keyword: " ,keyword);

    if (!isAppend) {
      this.messagePageInfo.resetPage();
    }

    this.currentMessageKeyword = keyword;

    return this.dashboardService.getMessageByKeyword(keyword, this.messagePageInfo).toPromise().then((data) => {
      if (isAppend) {

        if (data.length > 0) {
          this.dashboardMessageList = [...this.dashboardMessageList, ...data];
        }
      }
      else {
        this.dashboardMessageList = [...data];
      }
    });
  }

  public onChangeCommitmentStatus(event) {
    console.log("into onChangeCommitmentStatus event:", event, "messageId:", event.messageId, "keyword:", event.keyword);
    this.dashboardService.changeMessageStatus(event.messageId, "LinkStatus", "Done").toPromise().then(resp => {
      console.log("onChangeCommitmentStatus resp", resp);
      this.doFilterByKeyword(event.keyword, false);
    });
  }

  public async afterDisplayDeleteRemind() {

    await this.doFilterByKeyword(this.currentMessageKeyword, false);
    this.isDisplayDeleteRemind = false;
  }

  public async afterDisplayUpdateRemind() {

    await this.doFilterByKeyword(this.currentMessageKeyword, false);
    this.isDisplayUpdateRemind = false;
  }


  private getCustomerAutoDelete(cliendID): Observable<any> {

    return this.customerService.getAutoDeleteCustomerList(cliendID);
  }

  private getCustomerOverTime(cliendID): Observable<any> {
    return this.customerService.getOverTimeCustomerList(cliendID);
  }

  private async getCustomerBirthdayList(targetDate, subN, addN) {

    return this.customerService.getCustomerBirthdayList(targetDate, subN, addN).toPromise().then((data) => {
      this.birthdayList = data;
    })
  }

  private async getCalendarBetweenDate(startDate, endDate) {
    return this.calendarService.getCalendarEventList(startDate, endDate, '').toPromise();
  }


  public clickListShow() {
    this.isMobilePopupOpen = true;
  }

  public onClickMessage(message: DashboardMessage) {


    console.log("click message: ", message);
    let cliendID = message.clientID;
    if (message.status == 'UnRead') {
      this.dashboardService.updateMessageStatus(message.clientID, 'Reading').subscribe(data => {
      });
    }

    if (message.messageType == "AutoDelete") {
      this.getCustomerAutoDelete(cliendID).subscribe((data) => {
        this.alertAutoDeleteCustomer = data;
        this.isDisplayDeleteRemind = data.length > 0 ? true : false;
      });
    }

    else if (message.messageType == "Overtime") {
      this.getCustomerOverTime(cliendID).subscribe((data) => {
        this.alertOverTimeList = data;
        this.isDisplayUpdateRemind = data.length > 0 ? true : false;
      });
    }

    this.changeDetector.detectChanges();

  }

  public onClickCalendarItem(event) {
    //event = {showDate: "04/24", eventList: CalendarEventDetail}
    console.log("click event: ", event);
    this.calendarItemClientID = event.eventList.clientID;
    this.viewDate = new Date(event.showDate);
    this.calendarEventList = this.showCalendarList.filter(x => x.showDate == event.showDate)[0].eventList;
    //console.log("calendarEventList: ", this.calendarEventList);
    console.log("viewDate: ", this.viewDate);
    this.loadCalendarEventDetail(this.calendarItemClientID);
  }


  private loadCalendarEventDetail(clientID: string) {
    this.calendarService.getCalendarEventDetail(clientID).subscribe(data => {

      console.log('getCalendarEventDetail', data);
      this.calendarEventDetail = data;
      this.customerClientName = this.customerClientMap.get(this.calendarEventDetail.customerClientID);
      this.calendarEventDetail.displayActivity = this.profileCodeService.convertCode2Text('Calendar_Type', this.calendarEventDetail.activity);
      this.calendarEventDetail.displayAlert1 = this.profileCodeService.convertCode2Text('Calendar_RemindTime', this.calendarEventDetail.alert1);
      this.calendarEventDetail.displayAlert2 = this.profileCodeService.convertCode2Text('Calendar_RemindTime', this.calendarEventDetail.alert2);
      this.isExpandDetail = true;
      this.changeDetector.detectChanges();
    });
  }


  public editEvent() {
    this.isExpandEdit = true;
  }

  public delete() {
    this.isCalendarDelete = true;
  }

  public onClickDeleteConfirm() {

    this.calendarService.deleteCalendarEvent(this.calendarItemClientID).subscribe(data => {
      if (data.status) {
        this.isExpandDetail = false;
        this.isPopupDeleteSuccess = true;

        this.showCalendarList.forEach(item => {
          item.eventList = item.eventList.filter(x => x.clientID != this.calendarItemClientID);
        });

        this.showCalendarList = [...this.showCalendarList];
      }
    });

  }

  public onSaveCalendarEvent(resp) {

    let type = resp.type;
    let data = resp.data;
    if (type !== 'fail') {
      this.loadCalendarEventDetail(data.ClientID);
      this.getCalendarList();
      this.isDisplaySavePopup = true;
      this.isExpandEdit = false;
    }
    this.isSaveClick = false;
  }

  public onClickAppointmentSave() {
    this.isSaveClick = true;
  }

  private createCustomerList() {
    let pageInfo = new PageInfo();
    pageInfo.pageSize = -1;
    let filterCriteria = new CustomerFilterCriteria();
    this.customerService.getCustomerList(filterCriteria, pageInfo).subscribe((data) => {
      console.log('data', data);
      let temp_array = [];
      for (let custItem of data) {
        this.customerClientMap.set(custItem.clientID, this.customerUtils.convertNameToShow(custItem.firstName, custItem.lastName));
        temp_array.push(new SelectOption(custItem.clientID, this.customerUtils.convertNameToShow(custItem.firstName, custItem.lastName)));
      }
      this.customerItemList = temp_array;
    });
  }

}
