import { Component, OnInit, ErrorHandler } from '@angular/core';
import { take } from 'rxjs/operators';
import { Language, DefaultLoginMgr, LoginInfo, APPError, NumberUtils, AppRouter } from '@allianzSND/core';
import { DashboardService } from '../../service/dashboard-service.service';
import { ProgressService, Tag } from '@allianzSND/progress';

@Component({
  selector: 'snd-dashboard-performance-team',
  templateUrl: './dashboard-performance-team.component.html',
  styleUrls: ['./dashboard-performance-team.component.scss']
})
export class DashboardPerformanceTeamComponent implements OnInit {

  public dashboardPerformanceTeam: Array<Object> = [];
  public data: any = null;
  public isLoadDataFinish: boolean = false;

  public language = new Language();
  public windowWidth: number = null;
  private _monthlyProgressRate;
  public timeBaseTitle: string;
  public userRole: string;

  public ShowMonthlyProgressRateArr: Array<string>;
  public ShowMonthlyProgressRateBarArr: Array<string>;

  constructor(
    private dashboardService: DashboardService,
    private loginMgr: DefaultLoginMgr,
    private errorHandler: ErrorHandler,
    private router: AppRouter,
    private progressService: ProgressService,
  ) {
    this.init();
  }

  ngOnInit() {
    this.windowWidth = window.innerWidth;
  }
  private async init() {
    try {
      let loginInfo = await this.loginMgr.getLoginInfo().pipe(take(1)).toPromise();

      this.loginMgr.getLoginInfo().subscribe((info: LoginInfo) => {
        console.log('check permission subscribe:', info);
        if (info)
          this.userRole = info.AppUseMode[0];
        console.log("get AppUseMode", this.userRole);
      });
      this.data = await this.dashboardService.getPerformanceData("T", this.userRole).toPromise();
      if (this.data != null) {
        // this.isLoadDataFinish = true;
        // this.timeBaseTitle = this.data.timeBaseTitle;

        // console.log(" this.timeBaseTitle", this.timeBaseTitle);
        // this.data.datas.forEach((val,key) => {
        //   let MonthlyProgressRate = val["MonthlyProgressCurrent"] / val["MonthlyProgressTotal"];
        //   val["MonthlyProgressRate"] = this.convertNumberDigital(MonthlyProgressRate, 2);
        // });

        this.isLoadDataFinish = true;
        this.timeBaseTitle = this.data.timeBaseTitle;

        this.ShowMonthlyProgressRateArr = new Array<string>();
        this.ShowMonthlyProgressRateBarArr = new Array<string>();

        console.log(" this.timeBaseTitle", this.timeBaseTitle);
        this.data.datas.forEach((val, key) => {

          let MonthlyProgressRate = this.getRate(val["MonthlyProgressCurrent"], val["MonthlyProgressTotal"]);
          val["MonthlyProgressRate"] = this.convertNumberDigital(MonthlyProgressRate, 2);
          let pc = this.getPercentage(val["MonthlyProgressRate"]);

          let monthlyNumerator = this.convertToNumber(val["MonthlyProgressCurrent"]);
          let monthlyDenominator = this.convertToNumber(val["MonthlyProgressTotal"]);

          //let pcb = this.getPercentageBar(MonthlyProgressRate);

          let pcb = this.getMonthlyBarLength(monthlyNumerator, monthlyDenominator, 1.5, 150);
          this.ShowMonthlyProgressRateArr.push(pc);
          this.ShowMonthlyProgressRateBarArr.push(pcb);
        });

      }
    } catch (error) {
      this.errorHandler.handleError(new APPError('F00600', 'DashboardPerformanceTeam init fail!' + error.message));
    }
  }

  private getRate(a: any, b: any): number {
    if (a == -1 || !NumberUtils.isNumber(a)) return -1;
    if (b == -1 || !NumberUtils.isNumber(b)) return -1;
    return a / b;
  }

  private getPercentage(a: any): string {
    if (a == -1 || !NumberUtils.isNumber(a)) return "--";
    return (a * 100).toString();
  }

  private getPercentageBar(a: any): string {
    if (a == -1 || !NumberUtils.isNumber(a)) return "0";
    return (a).toString();
  }

  private convertToNumber(str:string):number {
    let convert = Number(str);

    if(convert == NaN) {
      return 0;
    } 
    else if(convert <= -1) {
      return 0;
    }
    else if(convert == 0) {
      return 0;
    }
    else {
      return convert;
    }
  }

  private getMonthlyBarLength(monthlyNumerator:number, monthlyDenominator:number, maxLength:number, maxBase: number):string {

    if(monthlyNumerator == 0 || monthlyDenominator == 0) {
      return "0";
    }

    let ans = Math.round((monthlyNumerator / monthlyDenominator) * 100) / maxBase;
    return (ans >= maxLength) ? maxLength.toString() : ans.toString();
  }

  //
  private convertNumberDigital(num: number, digit: number): number {
    if (digit === 0)
      return Math.round(num);
    else if (digit > 0) {
      return Math.round(num * Math.pow(10, digit)) / (Math.pow(10, digit));
    }
    else {
      return num;
    }
  }

  //000,000,000
  public convertNumberThousand(num: number): string {

    if (num <= -1 || !NumberUtils.isNumber(num)) return "--";

    var numString = num.toString();
    var pattern = /(-?\d+)(\d{3})/;

    while (pattern.test(numString)) {
      numString = numString.replace(pattern, "$1,$2");
    }
    return numString;
  }

  public goToProgress() {
    let tag = Tag.TeamTag;
    this.progressService.SetCurrentNavigationTag(tag);
    this.router.navigate("Progress");
  }

}
