import { Component, OnInit, Input, Output, EventEmitter, ErrorHandler, Optional, Inject } from '@angular/core';
import { Language, TranslateService, DefaultLoginMgr, LoginInfo, APPError, NotificationMgr, DataSyncService, NotificationType, AppRouter, showRuleToken, showRule } from '@allianzSND/core';
import { DashboardMessage } from '../../service/model/DashboardMessage';
import { getYear, getMonth, getDate, format } from 'date-fns';
import { ROLE, AgencyPlanStoreService, AGENCY_STATE, AgencyPlanAgentInfo, AgencyPlanStatus } from '@allianzSND/goal';
import { showDashboardRuleToken } from '../../injectionToken/injection-token';
import { showDashboardRule } from '../../interface/showDashboardRule.interface';
import { DashboardMessageType } from './dashboard-message-type';
import { take, takeUntil, first } from 'rxjs/operators';


@Component({
  selector: 'snd-dashboard-message',
  templateUrl: './dashboard-message.component.html',
  styleUrls: ['./dashboard-message.component.scss']
})
export class DashboardMessageComponent implements OnInit {

  public MsgType = 'UnRead';
  public Language = new Language();
  public IsHasMsg = false;
  public IsCAO = false;
  private _messageList: Array<DashboardMessage> = [];
  private keyword: string = 'UnRead';
  public msgType: Array<{ value: DashboardMessageType, displayName: string }> = [];
  private defaultMsgType: Array<{ value: DashboardMessageType, displayName: string }>
    = [{
      value: DashboardMessageType.Unread,
      displayName: this.Language.unread
    }, {
      value: DashboardMessageType.All,
      displayName: this.Language.all
    }, {
      value: DashboardMessageType.GoalSetting,
      displayName: this.Language.goalSettingMsgType
    }, 
    // {
    //   value: DashboardMessageType.Progress,
    //   displayName: this.Language.progress
    // }, 
    {
      value: DashboardMessageType.Customer,
      displayName: this.Language.customer
    }, {
      value: DashboardMessageType.PendingApproval,
      displayName: this.Language.pendingApproval
    }]

  public ConvertToImgUrl = {
    "GoalSetting": 'goal',
    "Customer": 'cust',
    // "Progress": 'progress'
  }

  public ConvertCategoryToLanguage = {
    "GoalSetting": 'goalSetting',
    "Customer": 'customer',
    // "Progress": 'progress'
  }

  public ConvertStatus = {
    "Reading": true,
    "UnRead": false
  }

  constructor(
    private translateService: TranslateService,
    private loginMgr: DefaultLoginMgr,
    private errorHandler: ErrorHandler,
    private agencyPlanStoreService: AgencyPlanStoreService,
    private router: AppRouter,
    private notificationMgr: NotificationMgr,
    private syncService: DataSyncService,
    @Optional() @Inject(showDashboardRuleToken) private showDashboardRule: showDashboardRule,
    @Optional() @Inject(showRuleToken) private showRule: showRule,
  ) {
    this.init();
  }

  ngOnInit() { }

  //#region  getter setter (input output)

  @Output()
  public filterByKeyword: EventEmitter<string> = new EventEmitter();
  @Output()
  public clickMessage: EventEmitter<DashboardMessage> = new EventEmitter();
  @Output()
  public changeCommitmentStatus: EventEmitter<any> = new EventEmitter();

  @Input()
  set messageList(messageList) {
    if (messageList.length > 0) {
      this.IsHasMsg = true;
      console.log('isHasMsg is true');
    }
    else {
      this.IsHasMsg = false;
    }


    // if(this.showRule) {
    //   messageList.forEach(x => {
    //     x.messageDate = this.showRule.convertDate(new Date(x.messageDate));
    //   });
    // }

    // else {
    //   messageList.forEach(x => {
    //     x.messageDate = format(new Date(x.messageDate),'MM/dd/yyyy');
    //   });
    // }

    this._messageList = messageList;

    console.log("detail message: ", this._messageList);
  }
  get messageList() {
    return this._messageList;
  }

  //#endregion

  //#region function

  private async init() {
    console.log("Dashboard init");
    let loginInfo: LoginInfo = await this.loginMgr.getLoginInfo().pipe(take(1)).toPromise();
    console.log("Dashboard init loginInfo:", loginInfo);
    if (this.showDashboardRule) {
      let filtered = this.showDashboardRule.showMessageType(loginInfo);
      this.msgType = this.defaultMsgType.filter(x => filtered.indexOf(x.value) > -1);
    }
    else
      this.msgType = this.defaultMsgType;
    console.log("Dashboard init end:", this.msgType);
  }

  public Filter(keyword) {
    this.keyword = keyword;
    this.filterByKeyword.emit(keyword);
  }

  public OnClickMessage(message: DashboardMessage) {
    if (message.isClick === 'Y') {
      console.log("message", message)
      let argument = JSON.parse(message.argument);
      console.log("argument", argument, "typeof argument", typeof argument);
      if (message.isPopup === 'Y') {
        this.clickMessage.emit(message);
        this.filterByKeyword.emit(this.keyword);
        console.log('run push()');
        this.notificationMgr.pushNotification(NotificationType[message.messageType], argument);
      } else if (message.isPopup === 'N') {
        console.log("" + argument.agentID + "commitment");
        this.clickMessage.emit(message);
        this.filterByKeyword.emit(this.keyword);
        // get
        let agencyDetail: AgencyPlanAgentInfo = new AgencyPlanAgentInfo(argument['agentID'], argument['agentName'], argument['DataYear'], argument['AppUseMode']);
        // agencyDetail['agentID'] = argument['agentID'];
        // agencyDetail['agentName'] = argument['agentName'];
        // agencyDetail['dataYear'] = argument['DataYear'];
        // agencyDetail['appUseMode'] = argument['AppUseMode'];
        console.log("agencyDetail", agencyDetail);
        this.agencyPlanStoreService.setCurrentAgencyDetail(agencyDetail);
        this.router.navigate("commitment");

        // this._location.go('')  //commitment
      }
    } else if (message.isClick === 'N') {
      console.log('this is no action for click');
    }
  }

  //#endregion

}
