export class DashboardShowLoginInfo {
    
    private _showName: string;
    private _imgUrl: string;

    constructor(showName, imgUrl) {
        this._showName = showName;
        this._imgUrl = imgUrl;
    }

    get showName(): string {
        return this._showName;
    }

    get imgUrl(): string {
        return this._imgUrl;
    }
}